<template>
	<view>
		
	</view>
</template>

<script>
	export default {
		data() {
			return {
			  ifOnShow: false // 页面刷新
			}
		},
		methods: {
			
		},
		
		onShow() {
			// #ifdef H5
		    if(this.ifOnShow){
		      setTimeout(() => {
				location.reload()  
			  },0.1)
		    }
			// #endif
		},
		onHide(){
			// #ifdef H5
		    this.ifOnShow = true
			// #endif
		}
		
	}
</script>

